<!-- by 添少 -->
<template>
  <section>
    <topbar>
      <template slot="before">
        <b class="iconfont icon-back" @click="$router.go(-1)"></b>
      </template>
      教练详情
    </topbar>
    <section class="preview">
      <div class="cover">
        <img class="db fit-cover" :src="coach.cover"/>
      </div>
      <div class="text">
        <h3>{{coach.title}}</h3>
        <p class="info">{{coach.info}}</p>
        <p>{{coach.summary}}</p>
      </div>
    </section>
    <section class="layout">
      <ul class="ui-group">
        <li class="ui-cell">
          <div class="cell-title">创建的俱乐部</div>
          <div class="cell-value">无<b class="iconfont icon-arrow-right"></b></div>
        </li>
        <li class="ui-cell">
          <div class="cell-title">创建的俱乐部</div>
          <div class="cell-value">Sasa俱乐部<b class="iconfont icon-arrow-right"></b></div>
        </li>
      </ul>
    </section>
    <section class="detail">
      <tab :select.sync="tab.select" :data="tab.list" @change="handChangeTab"></tab>
      <template v-if="tab.select===0">
        <section class="tc1">
          <template v-for="i in 10">
            <div class="ui-thumb">
              <div class="cover">
                <img class="db fit-cover" :src="coach.cover"/>
              </div>
              <div class="text">
                <h4 class="l-e l1">{{tab.select}}塞班岛{{i}}</h4>
                <p class="info">Sasa俱乐部<b>俱乐部</b></p>
                <p class="l-e l2">目的地简介，行程的亮点等等的摘要信息放到这里</p>
              </div>
            </div>
          </template>
        </section>
      </template>
      <template v-if="tab.select===1">
        <section class="tc1">
          <template v-for="i in 10">
            <div class="ui-thumb">
              <div class="cover">
                <img class="db fit-cover" :src="coach.cover"/>
              </div>
              <div class="text">
                <h4 class="l-e l1">{{tab.select}}塞班岛{{i}}</h4>
                <p class="info">Sasa俱乐部<b>俱乐部</b></p>
                <p class="l-e l2">目的地简介，行程的亮点等等的摘要信息放到这里</p>
              </div>
            </div>
          </template>
        </section>
      </template>
      <template v-if="tab.select===2">
        <section class="tc1">
          <template v-for="i in 10">
            <div class="ui-thumb">
              <div class="cover">
                <img class="db fit-cover" :src="coach.cover"/>
              </div>
              <div class="text">
                <h4 class="l-e l1">{{tab.select}}塞班岛{{i}}</h4>
                <p class="info">Sasa俱乐部<b>俱乐部</b></p>
                <p class="l-e l2">目的地简介，行程的亮点等等的摘要信息放到这里</p>
              </div>
            </div>
          </template>
        </section>
      </template>
      <template v-if="tab.select===3">
        <section class="tc1">
          <template v-for="i in 10">
            <div class="ui-thumb">
              <div class="cover">
                <img class="db fit-cover" :src="coach.cover"/>
              </div>
              <div class="text">
                <h4 class="l-e l1">{{tab.select}}塞班岛{{i}}</h4>
                <p class="info">Sasa俱乐部<b>俱乐部</b></p>
                <p class="l-e l2">目的地简介，行程的亮点等等的摘要信息放到这里</p>
              </div>
            </div>
          </template>
        </section>
      </template>
    </section>
  </section>
</template>

<script>
  import { mapState } from 'vuex'
  import Topbar from '@/components/topbar'
  import Tab from '@/components/tab'

  export default {
    name: 'coach',
    components: {
      Topbar,
      Tab
    },
    computed: {
      ...mapState({}),
    },
    data () {
      return {
        coach: {
          cover: 'data:image/jpeg;base64,',
          title: 'Kevin',
          info: '28岁 巴厘岛 英语/法语',
          summary: 'Kevin的简介，Kevin的格言？'
        },
        tab: {
          select: 0,
          list: ['课程', '行程', '装备', '帖子']
        },
      }
    },
    methods: {
      handChangeTab (index) {
        console.log(index)
      }
    }
  }
</script>

<style lang="scss" scoped>
  .ui-group {

  }

  .ui-cell {
    padding-top: 7px;
    padding-bottom: 7px;
    border-style: solid;
    border-width: 1px 0;
    border-color: #e5e5e5;
    display: flex;
    .cell-title {
      color: #888;
      padding-left: 15px;
      flex: 1;
    }
    .cell-value {
      padding-right: 10px;
      flex: 0 0 auto;
      .iconfont {
        color: #888;
        margin-left: 10px;
      }
    }
    & + .ui-cell {
      border-top-width: 0;
    }
  }

  .ui-thumb {
    font-size: 14px;
    display: flex;
    .cover {
      height: 100px;
      flex: 0 0 130px;
    }
    .text {
      text-align: justify;
      padding-left: 10px;
      h4 {
        font-size: 18px;
      }
      .info {
        color: #888;
        b {
          background-color: #d33c44;
          font-size: 11px;
          color: #fff;
          padding: 0 5px;
          margin-left: 0.5em;
          display: inline-block;
          border-radius: 5px;
        }
      }
      flex: 1;
    }
  }

  .ui-button {
    background-color: #fff;
    padding: 6px 10px;
    border-radius: 10px;
    border-width: 0;
    &.primary {
      background-color: #1797c6;
      color: #fff;
    }
    &.disabled {
      background-color: #ccc;
      color: #fff;
    }
  }

  .layout {
    background-color: #fff;
    margin-bottom: 10px;
  }

  .preview {
    background-color: #fff;
    color: #333;
    margin-bottom: 10px;
    .text {
      padding: 0 15px 10px;
      margin-bottom: 10px;
    }
    .info {
      font-size: 14px;
      color: #888;
    }

    h3 {
      font-size: 22px;
      font-weight: 600;
      padding-top: 10px;
    }
  }

  .detail {
    background-color: #fff;
    .tc1 {
      overflow: hidden;
      padding: 15px 15px 0;
      .ui-thumb {
        margin-bottom: 15px;
      }
    }
  }
</style>



